*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  
  background-image: linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%);
  color: rgb(255, 255, 255);
  min-height: 100vh;
}
header{
  font-size: 2rem;
}
header,
form{
  min-height: 20vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
form input,
form button{
  padding: 0.5rem;
  font-size: 2rem;
  border: none;
  background: white;
  border-radius: 10px;
  margin-right: 5px;
}
form button{
  color:white;
  background:  rgb(0, 183, 255);
  cursor: pointer;
  transition: all 0.3s ease;
}
form button:hover{
background:white;
color:  rgb(0, 183, 255);

}
.todo-container{
  display: flex;
  justify-content: center;
  align-items: center;
}
.todo-list{
  min-width: 30%;
  list-style: none;
}
.todo{
  margin: 0.5rem;
  background: white;
  font-size: 1.5rem;
  color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 1s ease;
  border-radius: 10px;
  margin-right: 15px;
}
.filter-todo{
  padding: 1rem;
  border-radius: 10px;
}
.todo li{
  flex: 1;
}
.trash-btn,
.complete-btn{
  background:  rgb(0, 183, 255);
  color: white;
  border: none;
  padding: 1rem;
  cursor: pointer;
  font-size: 1rem;
}
.complete-btn{
  background: rgb(64, 224, 85);
  transition: all 0.3s ease;
}
.complete-btn :hover{
  background: white;
  color: turquoise;
}
.todo-item{
  padding: 0rem 0.5rem;
}
.fa-trash,
.fa-check{
  pointer-events: none;
}
.fall{
  transform: translateY(10rem) rotate(20deg);
  opacity: 0;
}
.completed{
  text-decoration: line-through;
}
select{
  -webkit-appearance: none;
  -moz-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0 !important;
  background-image: none;
}
.select{
  margin: 1rem;
  position: relative;
  overflow: hidden;
}
select{
  color:  rgb(0, 183, 255);
  cursor: pointer;
  width: 12rem;
  

}
.select::after{
  content: "\25BC";
  position: absolute;
  top: 0;
  right: 0;
  padding: 1rem;
  background:  rgb(0, 183, 255);
  cursor: pointer;
  pointer-events: none;
  border-radius: 5px;
  border-left: 10px;
}
